import {
    CheckCircleOutlined,
    DeleteOutlined,
    EditOutlined,
    KeyOutlined,
    MailOutlined,
    PhoneOutlined,
    PlusOutlined,
    SearchOutlined,
    StopOutlined,
    TeamOutlined,
    UserOutlined,
} from '@ant-design/icons';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import {
    Badge,
    Button,
    Card,
    Descriptions,
    Drawer,
    Form,
    Input,
    message,
    Modal,
    Popconfirm,
    Select,
    Space,
    Switch,
    Table,
    Tag,
    Tooltip,
    Typography,
} from 'antd';
import type { ColumnsType } from 'antd/es/table';
import dayjs from 'dayjs';
import { useState } from 'react';
import { settingsApi, type CreateUserData, type User, type UserDetail } from '../../features/settings/api';

const { Title, Text } = Typography;

export default function UsersPage() {
  const queryClient = useQueryClient();
  const [search, setSearch] = useState('');
  const [showInactive, setShowInactive] = useState(false);
  const [modalOpen, setModalOpen] = useState(false);
  const [editingUser, setEditingUser] = useState<UserDetail | null>(null);
  const [detailDrawerOpen, setDetailDrawerOpen] = useState(false);
  const [selectedUser, setSelectedUser] = useState<UserDetail | null>(null);
  const [resetPasswordModal, setResetPasswordModal] = useState(false);
  const [resetPasswordUserId, setResetPasswordUserId] = useState<string | null>(null);
  const [form] = Form.useForm();
  const [resetPasswordForm] = Form.useForm();

  const showFormErrors = (error: { errorFields?: Array<{ name: (string | number)[]; errors: string[] }> }) => {
    const errorFields = error?.errorFields || [];
    if (errorFields.length === 0) {
      message.warning('  ');
      return;
    }
    const messages = errorFields
      .map((field) => `${field.name.join('.')}: ${field.errors.join(', ')}`)
      .join('; ');
    message.error(messages);
  };

  // Fetch users
  const { data: usersData, isLoading } = useQuery({
    queryKey: ['users', search, showInactive],
    queryFn: () => settingsApi.users.list({ 
      search: search || undefined,
      is_active: showInactive ? undefined : true,
    }),
  });

  // Fetch roles for select
  const { data: rolesData } = useQuery({
    queryKey: ['roles'],
    queryFn: () => settingsApi.roles.list(),
  });

  const users = usersData?.results || [];
  const roles = rolesData?.results || [];

  // Create user mutation
  const createMutation = useMutation({
    mutationFn: (data: CreateUserData) => settingsApi.users.create(data),
    onSuccess: () => {
      message.success(' ');
      queryClient.invalidateQueries({ queryKey: ['users'] });
      setModalOpen(false);
      form.resetFields();
    },
    onError: (error: Error & { response?: { data?: Record<string, string[]> } }) => {
      const errorData = error.response?.data as unknown;
      if (errorData && typeof errorData === 'object') {
        const messages = Object.entries(errorData as Record<string, string[] | string>)
          .map(([field, errors]) =>
            Array.isArray(errors)
              ? `${field}: ${errors.join(', ')}`
              : `${field}: ${String(errors)}`
          )
          .join('; ');
        message.error(messages || '  ');
      } else if (typeof errorData === 'string') {
        message.error(errorData);
      } else {
        message.error('  ');
      }
    },
  });

  // Update user mutation
  const updateMutation = useMutation({
    mutationFn: ({ id, data }: { id: string; data: Partial<UserDetail> }) =>
      settingsApi.users.update(id, data),
    onSuccess: () => {
      message.success(' ');
      queryClient.invalidateQueries({ queryKey: ['users'] });
      setModalOpen(false);
      setEditingUser(null);
      form.resetFields();
    },
    onError: () => message.error(' '),
  });

  // Delete user mutation
  const deleteMutation = useMutation({
    mutationFn: (id: string) => settingsApi.users.delete(id),
    onSuccess: () => {
      message.success(' ');
      queryClient.invalidateQueries({ queryKey: ['users'] });
    },
    onError: () => message.error(' '),
  });

  // Activate/Deactivate mutations
  const activateMutation = useMutation({
    mutationFn: (id: string) => settingsApi.users.activate(id),
    onSuccess: () => {
      message.success(' ');
      queryClient.invalidateQueries({ queryKey: ['users'] });
    },
    onError: () => message.error(' '),
  });

  const deactivateMutation = useMutation({
    mutationFn: (id: string) => settingsApi.users.deactivate(id),
    onSuccess: () => {
      message.success(' ');
      queryClient.invalidateQueries({ queryKey: ['users'] });
    },
    onError: () => message.error(' '),
  });

  // Reset password mutation
  const resetPasswordMutation = useMutation({
    mutationFn: ({ id, password }: { id: string; password: string }) =>
      settingsApi.users.resetPassword(id, password),
    onSuccess: () => {
      message.success(' ');
      setResetPasswordModal(false);
      setResetPasswordUserId(null);
      resetPasswordForm.resetFields();
    },
    onError: () => message.error('  '),
  });

  // Fetch single user for detail view
  const { data: userDetail } = useQuery({
    queryKey: ['user', selectedUser?.id],
    queryFn: () => settingsApi.users.get(selectedUser!.id),
    enabled: !!selectedUser?.id && detailDrawerOpen,
  });

  const handleCreate = () => {
    setEditingUser(null);
    form.resetFields();
    setModalOpen(true);
  };

  const handleEdit = async (user: User) => {
    const detail = await settingsApi.users.get(user.id);
    setEditingUser(detail);
    form.setFieldsValue({
      ...detail,
      role_ids: detail.user_roles.map(ur => ur.role),
    });
    setModalOpen(true);
  };

  const handleViewDetail = async (user: User) => {
    const detail = await settingsApi.users.get(user.id);
    setSelectedUser(detail);
    setDetailDrawerOpen(true);
  };

  const handleSubmit = async (values: Record<string, unknown>) => {
    if (editingUser) {
      updateMutation.mutate({
        id: editingUser.id,
        data: values as unknown as Partial<UserDetail>,
      });
    } else {
      createMutation.mutate(values as unknown as CreateUserData);
    }
  };

  const handleResetPassword = (userId: string) => {
    setResetPasswordUserId(userId);
    setResetPasswordModal(true);
  };

  const submitResetPassword = (values: { new_password: string }) => {
    if (resetPasswordUserId) {
      resetPasswordMutation.mutate({
        id: resetPasswordUserId,
        password: values.new_password,
      });
    }
  };

  const columns: ColumnsType<User> = [
    {
      title: '',
      key: 'user',
      width: 280,
      render: (_, record) => (
        <Space direction="vertical" size={0}>
          <Space>
            <UserOutlined />
            <Text strong style={{ cursor: 'pointer' }} onClick={() => handleViewDetail(record)}>
              {record.full_name || record.username}
            </Text>
          </Space>
          <Text type="secondary" style={{ fontSize: 12 }}>@{record.username}</Text>
        </Space>
      ),
    },
    {
      title: 'Email',
      dataIndex: 'email',
      key: 'email',
      width: 220,
      render: (email) => (
        <Space>
          <MailOutlined />
          <Text>{email}</Text>
        </Space>
      ),
    },
    {
      title: ' / ',
      key: 'position',
      width: 200,
      render: (_, record) => (
        <Space direction="vertical" size={0}>
          <Text>{record.position || ''}</Text>
          <Text type="secondary" style={{ fontSize: 12 }}>{record.department || ''}</Text>
        </Space>
      ),
    },
    {
      title: '',
      dataIndex: 'roles_display',
      key: 'roles',
      width: 180,
      render: (rolesArr: string[]) => (
        <Space wrap size={4}>
          {rolesArr && rolesArr.length > 0 ? (
            rolesArr.map((role, idx) => (
              <Tag key={idx} color="blue">{role}</Tag>
            ))
          ) : (
            <Text type="secondary"></Text>
          )}
        </Space>
      ),
    },
    {
      title: '',
      dataIndex: 'is_active',
      key: 'is_active',
      width: 100,
      align: 'center',
      render: (isActive) =>
        isActive ? (
          <Badge status="success" text="" />
        ) : (
          <Badge status="default" text="" />
        ),
    },
    {
      title: ' ',
      dataIndex: 'last_login',
      key: 'last_login',
      width: 150,
      render: (date) =>
        date ? dayjs(date).format('DD.MM.YYYY HH:mm') : <Text type="secondary"></Text>,
    },
    {
      title: '',
      key: 'actions',
      width: 150,
      fixed: 'right',
      render: (_, record) => (
        <Space size={4}>
          <Tooltip title="">
            <Button size="small" icon={<EditOutlined />} onClick={() => handleEdit(record)} />
          </Tooltip>
          <Tooltip title=" ">
            <Button size="small" icon={<KeyOutlined />} onClick={() => handleResetPassword(record.id)} />
          </Tooltip>
          {record.is_active ? (
            <Popconfirm
              title=" ?"
              onConfirm={() => deactivateMutation.mutate(record.id)}
              okText=""
              cancelText=""
            >
              <Tooltip title="">
                <Button size="small" danger icon={<StopOutlined />} />
              </Tooltip>
            </Popconfirm>
          ) : (
            <Tooltip title="">
              <Button
                size="small"
                type="primary"
                ghost
                icon={<CheckCircleOutlined />}
                onClick={() => activateMutation.mutate(record.id)}
              />
            </Tooltip>
          )}
          <Popconfirm
            title=" ?"
            description="   "
            onConfirm={() => deleteMutation.mutate(record.id)}
            okText=""
            okType="danger"
            cancelText=""
          >
            <Tooltip title="">
              <Button size="small" danger icon={<DeleteOutlined />} />
            </Tooltip>
          </Popconfirm>
        </Space>
      ),
    },
  ];

  return (
    <div className="page-container">
      <div style={{ marginBottom: 24, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <div>
          <Title level={4} style={{ margin: 0 }}></Title>
          <Text type="secondary">  </Text>
        </div>
        <Button type="primary" icon={<PlusOutlined />} onClick={handleCreate}>
           
        </Button>
      </div>

      <Card size="small" style={{ marginBottom: 16 }}>
        <Space wrap>
          <Input
            placeholder="  , email..."
            prefix={<SearchOutlined />}
            style={{ width: 300 }}
            allowClear
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
          <Switch
            checkedChildren=" "
            unCheckedChildren=" "
            checked={showInactive}
            onChange={setShowInactive}
          />
        </Space>
      </Card>

      <Card size="small">
        <Table
          columns={columns}
          dataSource={users}
          rowKey="id"
          loading={isLoading}
          pagination={{
            pageSize: 20,
            showSizeChanger: true,
            showTotal: (total) => `: ${total} `,
          }}
          scroll={{ x: 1200 }}
          size="small"
        />
      </Card>

      {/* Create/Edit Modal */}
      <Modal
        title={editingUser ? ' ' : ' '}
        open={modalOpen}
        onCancel={() => {
          setModalOpen(false);
          setEditingUser(null);
          form.resetFields();
        }}
        onOk={() => {
          form
            .validateFields()
            .then(handleSubmit)
            .catch(showFormErrors);
        }}
        okText={editingUser ? '' : ''}
        cancelText=""
        width={600}
        confirmLoading={createMutation.isPending || updateMutation.isPending}
      >
        <Form
          form={form}
          layout="vertical"
          onFinish={handleSubmit}
          onFinishFailed={showFormErrors}
          initialValues={{ is_active: true }}
        >
          <Space size="middle" style={{ display: 'flex' }}>
            <Form.Item
              name="username"
              label=""
              rules={[
                { required: true, message: ' ' },
                { whitespace: true, message: '    ' },
              ]}
              style={{ flex: 1 }}
            >
              <Input prefix={<UserOutlined />} placeholder="  " disabled={!!editingUser} />
            </Form.Item>
            <Form.Item
              name="email"
              label="Email"
              rules={[
                { required: true, message: ' email' },
                { type: 'email', message: ' email' },
              ]}
              style={{ flex: 1 }}
            >
              <Input prefix={<MailOutlined />} placeholder="email@example.com" />
            </Form.Item>
          </Space>

          {!editingUser && (
            <Space size="middle" style={{ display: 'flex' }}>
              <Form.Item
                name="password"
                label=""
                rules={[{ required: true, message: ' ' }]}
                style={{ flex: 1 }}
              >
                <Input.Password placeholder="" />
              </Form.Item>
              <Form.Item
                name="password_confirm"
                label=""
                rules={[
                  { required: true, message: ' ' },
                  ({ getFieldValue }) => ({
                    validator(_, value) {
                      if (!value || getFieldValue('password') === value) {
                        return Promise.resolve();
                      }
                      return Promise.reject(new Error('  '));
                    },
                  }),
                ]}
                style={{ flex: 1 }}
              >
                <Input.Password placeholder=" " />
              </Form.Item>
            </Space>
          )}

          <Space size="middle" style={{ display: 'flex' }}>
            <Form.Item name="last_name" label="" style={{ flex: 1 }}>
              <Input placeholder="" />
            </Form.Item>
            <Form.Item name="first_name" label="" style={{ flex: 1 }}>
              <Input placeholder="" />
            </Form.Item>
            <Form.Item name="middle_name" label="" style={{ flex: 1 }}>
              <Input placeholder="" />
            </Form.Item>
          </Space>

          <Space size="middle" style={{ display: 'flex' }}>
            <Form.Item name="position" label="" style={{ flex: 1 }}>
              <Input placeholder="" />
            </Form.Item>
            <Form.Item name="department" label="" style={{ flex: 1 }}>
              <Input placeholder=" " />
            </Form.Item>
          </Space>

          <Form.Item name="phone" label="">
            <Input prefix={<PhoneOutlined />} placeholder="+7 (999) 123-45-67" />
          </Form.Item>

          <Form.Item name="role_ids" label="">
            <Select
              mode="multiple"
              placeholder=" "
              options={roles.map((r) => ({ label: r.name, value: r.id }))}
              optionFilterProp="label"
            />
          </Form.Item>

          <Space size="middle">
            <Form.Item name="is_active" valuePropName="checked" label="">
              <Switch />
            </Form.Item>
          </Space>
        </Form>
      </Modal>

      {/* Reset Password Modal */}
      <Modal
        title=" "
        open={resetPasswordModal}
        onCancel={() => {
          setResetPasswordModal(false);
          setResetPasswordUserId(null);
          resetPasswordForm.resetFields();
        }}
        onOk={() => resetPasswordForm.submit()}
        okText=""
        cancelText=""
        confirmLoading={resetPasswordMutation.isPending}
      >
        <Form form={resetPasswordForm} layout="vertical" onFinish={submitResetPassword}>
          <Form.Item
            name="new_password"
            label=" "
            rules={[
              { required: true, message: '  ' },
              { min: 8, message: '    8 ' },
            ]}
          >
            <Input.Password placeholder=" " />
          </Form.Item>
          <Form.Item
            name="confirm_password"
            label=" "
            rules={[
              { required: true, message: ' ' },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (!value || getFieldValue('new_password') === value) {
                    return Promise.resolve();
                  }
                  return Promise.reject(new Error('  '));
                },
              }),
            ]}
          >
            <Input.Password placeholder=" " />
          </Form.Item>
        </Form>
      </Modal>

      {/* User Detail Drawer */}
      <Drawer
        title="  "
        open={detailDrawerOpen}
        onClose={() => {
          setDetailDrawerOpen(false);
          setSelectedUser(null);
        }}
        width={500}
      >
        {(userDetail || selectedUser) && (
          <div>
            <Descriptions column={1} bordered size="small">
              <Descriptions.Item label="">
                {(userDetail || selectedUser)?.username}
              </Descriptions.Item>
              <Descriptions.Item label="">
                {(userDetail || selectedUser)?.full_name || ''}
              </Descriptions.Item>
              <Descriptions.Item label="Email">
                {(userDetail || selectedUser)?.email}
              </Descriptions.Item>
              <Descriptions.Item label="">
                {userDetail?.phone || ''}
              </Descriptions.Item>
              <Descriptions.Item label="">
                {(userDetail || selectedUser)?.position || ''}
              </Descriptions.Item>
              <Descriptions.Item label="">
                {(userDetail || selectedUser)?.department || ''}
              </Descriptions.Item>
              <Descriptions.Item label="">
                {(userDetail || selectedUser)?.is_active ? (
                  <Badge status="success" text="" />
                ) : (
                  <Badge status="default" text="" />
                )}
              </Descriptions.Item>
              <Descriptions.Item label=" ">
                {userDetail?.date_joined
                  ? dayjs(userDetail.date_joined).format('DD.MM.YYYY HH:mm')
                  : ''}
              </Descriptions.Item>
              <Descriptions.Item label=" ">
                {(userDetail || selectedUser)?.last_login
                  ? dayjs((userDetail || selectedUser)?.last_login).format('DD.MM.YYYY HH:mm')
                  : ''}
              </Descriptions.Item>
            </Descriptions>

            <Title level={5} style={{ marginTop: 24 }}>
              <TeamOutlined /> 
            </Title>
            {userDetail?.user_roles && userDetail.user_roles.length > 0 ? (
              <Space wrap>
                {userDetail.user_roles.map((ur) => (
                  <Tag key={ur.id} color="blue" icon={<TeamOutlined />}>
                    {ur.role_detail?.name || ''}
                  </Tag>
                ))}
              </Space>
            ) : (
              <Text type="secondary">  </Text>
            )}

            <div style={{ marginTop: 24 }}>
              <Space>
                <Button icon={<EditOutlined />} onClick={() => {
                  setDetailDrawerOpen(false);
                  if (userDetail) handleEdit(userDetail);
                }}>
                  
                </Button>
                <Button icon={<KeyOutlined />} onClick={() => {
                  if (userDetail) {
                    handleResetPassword(userDetail.id);
                  }
                }}>
                   
                </Button>
              </Space>
            </div>
          </div>
        )}
      </Drawer>
    </div>
  );
}
