import { DeleteOutlined, EditOutlined, PlusOutlined, SearchOutlined } from '@ant-design/icons';
import { Button, Card, Form, Input, InputNumber, message, Modal, Popconfirm, Select, Space, Table, Tabs, Tag, Tooltip, Typography } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import { useState } from 'react';
import type { ManufacturingProblemReason, ManufacturingProblemSubreason } from '../../features/settings/api';
import {
    useCreateManufacturingProblemReason,
    useCreateManufacturingProblemSubreason,
    useDeleteManufacturingProblemReason,
    useDeleteManufacturingProblemSubreason,
    useManufacturingProblemReasons,
    useManufacturingProblemSubreasons,
    useUpdateManufacturingProblemReason,
    useUpdateManufacturingProblemSubreason,
} from '../../features/settings/hooks';

const { Title, Text } = Typography;

const SEVERITY_OPTIONS = [
  { value: 1, label: '', color: 'blue' },
  { value: 2, label: '', color: 'orange' },
  { value: 3, label: '', color: 'red' },
];

const getSeverityColor = (severity: number) => {
  return SEVERITY_OPTIONS.find(s => s.value === severity)?.color || 'default';
};

export default function ManufacturingProblemReasonsPage() {
  const [searchText, setSearchText] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingReason, setEditingReason] = useState<ManufacturingProblemReason | null>(null);
  const [form] = Form.useForm();

  const [isSubreasonModalOpen, setIsSubreasonModalOpen] = useState(false);
  const [editingSubreason, setEditingSubreason] = useState<ManufacturingProblemSubreason | null>(null);
  const [subreasonForm] = Form.useForm();

  const { data: reasonsData, isLoading } = useManufacturingProblemReasons();
  const createMutation = useCreateManufacturingProblemReason();
  const updateMutation = useUpdateManufacturingProblemReason();
  const deleteMutation = useDeleteManufacturingProblemReason();

  const { data: subreasonsData, isLoading: subreasonsLoading } = useManufacturingProblemSubreasons(editingReason?.id);
  const createSubreasonMutation = useCreateManufacturingProblemSubreason();
  const updateSubreasonMutation = useUpdateManufacturingProblemSubreason();
  const deleteSubreasonMutation = useDeleteManufacturingProblemSubreason();

  const reasons = reasonsData?.results || [];
  const subreasons = subreasonsData?.results || [];

  const filteredReasons = reasons.filter(
    (reason) =>
      reason.name.toLowerCase().includes(searchText.toLowerCase()) ||
      reason.code.toLowerCase().includes(searchText.toLowerCase())
  );

  const handleOpenModal = (reason?: ManufacturingProblemReason) => {
    if (reason) {
      setEditingReason(reason);
      form.setFieldsValue(reason);
    } else {
      setEditingReason(null);
      form.resetFields();
      form.setFieldsValue({
        severity: 2,
        sort_order: (reasons.length + 1) * 10,
      });
    }
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setEditingReason(null);
    form.resetFields();
    setIsSubreasonModalOpen(false);
    setEditingSubreason(null);
    subreasonForm.resetFields();
  };

  const openSubreasonModal = (subreason?: ManufacturingProblemSubreason) => {
    if (!editingReason) {
      message.info('  ,   .');
      return;
    }

    if (subreason) {
      setEditingSubreason(subreason);
      subreasonForm.setFieldsValue(subreason);
    } else {
      setEditingSubreason(null);
      subreasonForm.resetFields();
      subreasonForm.setFieldsValue({
        sort_order: (subreasons.length + 1) * 10,
      });
    }
    setIsSubreasonModalOpen(true);
  };

  const closeSubreasonModal = () => {
    setIsSubreasonModalOpen(false);
    setEditingSubreason(null);
    subreasonForm.resetFields();
  };

  const submitSubreason = async (values: any) => {
    if (!editingReason) return;

    try {
      if (editingSubreason) {
        await updateSubreasonMutation.mutateAsync({ id: editingSubreason.id, data: values });
        message.success(' ');
      } else {
        await createSubreasonMutation.mutateAsync({ ...values, reason: editingReason.id });
        message.success(' ');
      }
      closeSubreasonModal();
    } catch (error: any) {
      message.error(error.message || '  ');
    }
  };

  const deleteSubreason = async (id: string) => {
    try {
      await deleteSubreasonMutation.mutateAsync(id);
      message.success(' ');
    } catch (error: any) {
      message.error(error.message || '  ');
    }
  };

  const handleSubmit = async (values: any) => {
    try {
      if (editingReason) {
        await updateMutation.mutateAsync({ id: editingReason.id, data: values });
        message.success('  ');
      } else {
        await createMutation.mutateAsync(values);
        message.success('  ');
      }
      handleCloseModal();
    } catch (error: any) {
      message.error(error.message || ' ');
    }
  };

  const handleDelete = async (id: string) => {
    try {
      await deleteMutation.mutateAsync(id);
      message.success(' ');
    } catch (error: any) {
      message.error(error.message || ' ');
    }
  };

  const columns: ColumnsType<ManufacturingProblemReason> = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: '',
      dataIndex: 'severity',
      key: 'severity',
      width: 120,
      align: 'center',
      render: (value, record) => (
        <Tag color={getSeverityColor(value)}>{record.severity_display}</Tag>
      ),
      sorter: (a, b) => a.severity - b.severity,
    },
    {
      title: ' ',
      dataIndex: 'suggested_action',
      key: 'suggested_action',
      ellipsis: true,
    },
    {
      title: '',
      dataIndex: 'sort_order',
      key: 'sort_order',
      width: 80,
      align: 'center',
      sorter: (a, b) => a.sort_order - b.sort_order,
    },
    {
      title: '',
      key: 'actions',
      width: 100,
      align: 'center',
      render: (_, record) => (
        <Space size="small">
          <Tooltip title="">
            <Button
              type="text"
              icon={<EditOutlined />}
              onClick={() => handleOpenModal(record)}
            />
          </Tooltip>
          <Popconfirm
            title=" ?"
            description="   "
            onConfirm={() => handleDelete(record.id)}
            okText=""
            cancelText=""
          >
            <Tooltip title="">
              <Button type="text" danger icon={<DeleteOutlined />} />
            </Tooltip>
          </Popconfirm>
        </Space>
      ),
    },
  ];

  return (
    <div className="page-container">
      <div style={{ marginBottom: 24, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <div>
          <Title level={4} style={{ margin: 0 }}>  </Title>
          <Text type="secondary">    </Text>
        </div>
        <Button type="primary" icon={<PlusOutlined />} onClick={() => handleOpenModal()}>
           
        </Button>
      </div>

      <Card size="small" style={{ marginBottom: 16 }}>
        <Space wrap>
          <Input
            placeholder="    ..."
            prefix={<SearchOutlined />}
            style={{ width: 300 }}
            allowClear
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
          />
        </Space>
      </Card>

      <Card size="small">
        <Table
          columns={columns}
          dataSource={filteredReasons}
          rowKey="id"
          loading={isLoading}
          pagination={false}
          size="small"
        />
      </Card>

      <Modal
        title={editingReason ? ' ' : '  '}
        open={isModalOpen}
        onCancel={handleCloseModal}
        footer={null}
        width={720}
      >
        <Tabs
          defaultActiveKey="reason"
          items={[
            {
              key: 'reason',
              label: '',
              children: (
                <Form
                  form={form}
                  layout="vertical"
                  onFinish={handleSubmit}
                  style={{ marginTop: 16 }}
                >
                  <Form.Item
                    name="name"
                    label=""
                    rules={[
                      { required: true, message: ' ' },
                      { max: 100, message: ' 100 ' },
                    ]}
                  >
                    <Input placeholder=" " />
                  </Form.Item>

                  <Form.Item name="description" label="">
                    <Input.TextArea rows={2} placeholder=" ..." />
                  </Form.Item>

                  <Space size="large" style={{ display: 'flex' }}>
                    <Form.Item
                      name="severity"
                      label=""
                      rules={[{ required: true, message: ' ' }]}
                    >
                      <Select style={{ width: 150 }} options={SEVERITY_OPTIONS} />
                    </Form.Item>

                    <Form.Item
                      name="sort_order"
                      label=" "
                      rules={[{ required: true, message: ' ' }]}
                    >
                      <InputNumber min={0} max={1000} />
                    </Form.Item>
                  </Space>

                  <Form.Item name="suggested_action" label=" ">
                    <Input.TextArea rows={3} placeholder="     ..." />
                  </Form.Item>

                  <Form.Item style={{ marginBottom: 0, marginTop: 24 }}>
                    <Space>
                      <Button onClick={handleCloseModal}></Button>
                      <Button
                        type="primary"
                        htmlType="submit"
                        loading={createMutation.isPending || updateMutation.isPending}
                      >
                        {editingReason ? '' : ''}
                      </Button>
                    </Space>
                  </Form.Item>
                </Form>
              ),
            },
            {
              key: 'subreasons',
              label: '',
              children: (
                <div style={{ paddingTop: 8 }}>
                  {!editingReason ? (
                    <Text type="secondary">  ,   .</Text>
                  ) : (
                    <>
                      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 }}>
                        <Text type="secondary">  {editingReason.name}</Text>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => openSubreasonModal()}>
                           
                        </Button>
                      </div>

                      <Table
                        size="small"
                        rowKey="id"
                        loading={subreasonsLoading}
                        dataSource={subreasons}
                        pagination={false}
                        columns={[
                          { title: '', dataIndex: 'name', key: 'name' },
                          {
                            title: '',
                            dataIndex: 'sort_order',
                            key: 'sort_order',
                            width: 100,
                            align: 'center',
                          },
                          {
                            title: '',
                            key: 'actions',
                            width: 110,
                            align: 'center',
                            render: (_, record: ManufacturingProblemSubreason) => (
                              <Space size="small">
                                <Tooltip title="">
                                  <Button type="text" icon={<EditOutlined />} onClick={() => openSubreasonModal(record)} />
                                </Tooltip>
                                <Popconfirm
                                  title=" ?"
                                  onConfirm={() => deleteSubreason(record.id)}
                                  okText=""
                                  cancelText=""
                                >
                                  <Tooltip title="">
                                    <Button type="text" danger icon={<DeleteOutlined />} />
                                  </Tooltip>
                                </Popconfirm>
                              </Space>
                            ),
                          },
                        ]}
                        locale={{ emptyText: '  ' }}
                      />
                    </>
                  )}
                </div>
              ),
            },
          ]}
        />

        <Modal
          title={editingSubreason ? ' ' : ' '}
          open={isSubreasonModalOpen}
          onCancel={closeSubreasonModal}
          footer={null}
          destroyOnClose
        >
          <Form form={subreasonForm} layout="vertical" onFinish={submitSubreason} style={{ marginTop: 12 }}>
            <Form.Item
              name="name"
              label=""
              rules={[
                { required: true, message: ' ' },
                { max: 200, message: ' 200 ' },
              ]}
            >
              <Input placeholder=":   " />
            </Form.Item>

            <Form.Item name="sort_order" label=" " rules={[{ required: true, message: ' ' }]}>
              <InputNumber min={0} max={10000} />
            </Form.Item>

            <Form.Item style={{ marginBottom: 0, marginTop: 12 }}>
              <Space>
                <Button onClick={closeSubreasonModal}></Button>
                <Button
                  type="primary"
                  htmlType="submit"
                  loading={createSubreasonMutation.isPending || updateSubreasonMutation.isPending}
                >
                  {editingSubreason ? '' : ''}
                </Button>
              </Space>
            </Form.Item>
          </Form>
        </Modal>
      </Modal>
    </div>
  );
}
