import {
    BankOutlined,
    DeleteOutlined,
    EditOutlined,
    MailOutlined,
    PhoneOutlined,
    PlusOutlined, SearchOutlined,
    StarFilled,
    UserOutlined
} from '@ant-design/icons';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import {
    Avatar,
    Button,
    Card,
    Divider,
    Empty,
    Form,
    Input,
    List,
    message,
    Modal,
    Popconfirm,
    Rate,
    Space,
    Spin,
    Table,
    Tabs,
    Tag,
    Typography
} from 'antd';
import type { ColumnsType } from 'antd/es/table';
import { useState } from 'react';

import { type BankDetails, BankDetailsList, catalogApi, type ContactPerson, type Supplier } from '../../features/catalog';

const { Title, Text } = Typography;

export default function SuppliersPage() {
  const queryClient = useQueryClient();
  const [searchText, setSearchText] = useState('');
  const [modalOpen, setModalOpen] = useState(false);
  const [editingSupplier, setEditingSupplier] = useState<Supplier | null>(null);
  const [form] = Form.useForm();
  const [contactForm] = Form.useForm();
  
  //    
  const [contacts, setContacts] = useState<Partial<ContactPerson>[]>([]);
  const [contactModalOpen, setContactModalOpen] = useState(false);
  const [editingContact, setEditingContact] = useState<Partial<ContactPerson> | null>(null);
  const [editingContactIndex, setEditingContactIndex] = useState<number | null>(null);

  //    
  const [bankDetails, setBankDetails] = useState<BankDetails[]>([]);

  // Fetch suppliers
  const { data: suppliersData, isLoading } = useQuery({
    queryKey: ['suppliers', searchText],
    queryFn: () => catalogApi.suppliers.list({ search: searchText || undefined }),
    staleTime: 0,
    refetchOnMount: 'always',
  });

  const suppliers = suppliersData?.results || [];

  // Create/Update mutation
  const saveMutation = useMutation({
    mutationFn: async (data: Partial<Supplier>) => {
      let supplier: Supplier;
      if (editingSupplier) {
        supplier = await catalogApi.suppliers.update(editingSupplier.id, data);
        
        //    ,   
        const existingIds = new Set(editingSupplier.contacts?.map(c => c.id) || []);
        const currentIds = new Set(contacts.filter(c => c.id).map(c => c.id!));
        
        //  ,     
        for (const contact of editingSupplier.contacts || []) {
          if (!currentIds.has(contact.id)) {
            await catalogApi.contactPersons.delete(contact.id);
          }
        }
        
        //    
        for (const contact of contacts) {
          if (contact.id && existingIds.has(contact.id)) {
            //  
            await catalogApi.contactPersons.update(contact.id, {
              ...contact,
              supplier: supplier.id,
            });
          } else {
            //  
            await catalogApi.contactPersons.create({
              ...contact,
              supplier: supplier.id,
            });
          }
        }
      } else {
        supplier = await catalogApi.suppliers.create(data);
        
        //   
        for (const contact of contacts) {
          await catalogApi.contactPersons.create({
            ...contact,
            supplier: supplier.id,
          });
        }
      }
      
      return supplier;
    },
    onSuccess: () => {
      message.success(editingSupplier ? ' ' : ' ');
      queryClient.invalidateQueries({ queryKey: ['suppliers'] });
      handleModalClose();
    },
    onError: () => {
      message.error('   ');
    },
  });

  // Delete mutation
  const deleteMutation = useMutation({
    mutationFn: (id: string) => catalogApi.suppliers.delete(id),
    onSuccess: () => {
      message.success(' ');
      queryClient.invalidateQueries({ queryKey: ['suppliers'] });
    },
    onError: () => {
      message.error('   ');
    },
  });

  const handleModalClose = () => {
    setModalOpen(false);
    setEditingSupplier(null);
    form.resetFields();
    setContacts([]);
    setBankDetails([]);
  };

  const handleEdit = async (supplier: Supplier) => {
    //        
    try {
      const fullData = await catalogApi.suppliers.get(supplier.id);
      setEditingSupplier(fullData);
      //  rating  number  Rate 
      form.setFieldsValue({
        ...fullData,
        rating: fullData.rating != null ? parseFloat(String(fullData.rating)) : null,
      });
      setContacts(fullData.contacts || []);
      
      //   
      const bankDetailsData = await catalogApi.bankDetails.bySupplier(supplier.id);
      setBankDetails(bankDetailsData);
      
      setModalOpen(true);
    } catch (error) {
      message.error('   ');
    }
  };

  const handleAdd = () => {
    setEditingSupplier(null);
    form.resetFields();
    setContacts([]);
    setModalOpen(true);
  };

  const handleSubmit = async () => {
    const values = await form.validateFields();
    saveMutation.mutate(values);
  };

  //  
  const handleAddContact = () => {
    setEditingContact(null);
    setEditingContactIndex(null);
    contactForm.resetFields();
    setContactModalOpen(true);
  };

  const handleEditContact = (contact: Partial<ContactPerson>, index: number) => {
    setEditingContact(contact);
    setEditingContactIndex(index);
    contactForm.setFieldsValue(contact);
    setContactModalOpen(true);
  };

  const handleDeleteContact = (index: number) => {
    const newContacts = [...contacts];
    newContacts.splice(index, 1);
    //   ,  
    if (newContacts.length > 0 && !newContacts.find(c => c.is_primary)) {
      newContacts[0].is_primary = true;
    }
    setContacts(newContacts);
  };

  const handleSetPrimaryContact = (index: number) => {
    setContacts(contacts.map((c, i) => ({ ...c, is_primary: i === index })));
  };

  const handleContactSubmit = async () => {
    const values = await contactForm.validateFields();
    if (editingContactIndex !== null) {
      // 
      const newContacts = [...contacts];
      newContacts[editingContactIndex] = { ...newContacts[editingContactIndex], ...values };
      setContacts(newContacts);
    } else {
      // 
      const newContact = { 
        ...values, 
        is_primary: contacts.length === 0 
      };
      setContacts([...contacts, newContact]);
    }
    setContactModalOpen(false);
    contactForm.resetFields();
  };

  //     (    full_name  )
  const getContactFullName = (contact: Partial<ContactPerson>) => {
    if (contact.full_name) return contact.full_name;
    const parts = [contact.last_name, contact.first_name, contact.middle_name].filter(Boolean);
    return parts.join(' ') || ' ';
  };

  const columns: ColumnsType<Supplier> = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      render: (text: string, record: Supplier) => (
        <div>
          <div style={{ fontWeight: 500 }}>{text}</div>
          {record.full_name && <Text type="secondary" style={{ fontSize: 12 }}>{record.full_name}</Text>}
        </div>
      ),
    },
    {
      title: '',
      dataIndex: 'inn',
      key: 'inn',
      width: 130,
    },
    {
      title: ' ',
      dataIndex: 'primary_contact',
      key: 'primary_contact',
      width: 220,
      render: (contact: { full_name?: string; phone?: string } | null) => {
        if (!contact) return <Text type="secondary"></Text>;
        return (
          <div>
            <div><UserOutlined /> {contact.full_name || ' '}</div>
            {contact.phone && <Text type="secondary" style={{ fontSize: 12 }}><PhoneOutlined /> {contact.phone}</Text>}
          </div>
        );
      },
    },
    {
      title: '',
      dataIndex: 'contacts_count',
      key: 'contacts_count',
      width: 100,
      render: (count: number) => (
        <Tag>{count || 0}</Tag>
      ),
    },
    {
      title: '',
      dataIndex: 'rating',
      key: 'rating',
      width: 160,
      render: (rating: number | string | null) => (
        <Rate 
          disabled 
          value={rating != null ? parseFloat(String(rating)) : 0} 
          allowHalf 
          style={{ fontSize: 14 }} 
        />
      ),
    },
    {
      title: '',
      key: 'actions',
      width: 100,
      render: (_: unknown, record: Supplier) => (
        <Space>
          <Button type="text" icon={<EditOutlined />} onClick={() => handleEdit(record)} />
          <Popconfirm
            title=" ?"
            description="     "
            onConfirm={() => deleteMutation.mutate(record.id)}
          >
            <Button type="text" danger icon={<DeleteOutlined />} />
          </Popconfirm>
        </Space>
      ),
    },
  ];

  return (
    <div className="page-container">
      <div style={{ marginBottom: 24, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <div>
          <Title level={4} style={{ margin: 0 }}></Title>
          <Text type="secondary">    </Text>
        </div>
        <Button type="primary" icon={<PlusOutlined />} onClick={handleAdd}> </Button>
      </div>

      <Card size="small" style={{ marginBottom: 16 }}>
        <Space wrap>
          <Input
            placeholder="  , ..."
            prefix={<SearchOutlined />}
            style={{ width: 300 }}
            allowClear
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
          />
        </Space>
      </Card>

      <Card size="small">
        {isLoading ? (
          <div style={{ textAlign: 'center', padding: 40 }}><Spin /></div>
        ) : suppliers.length === 0 ? (
          <Empty description=" " />
        ) : (
          <Table
            dataSource={suppliers}
            columns={columns}
            rowKey="id"
            size="small"
            pagination={{ pageSize: 20 }}
          />
        )}
      </Card>

      {/*    */}
      <Modal
        title={editingSupplier ? ' ' : ' '}
        open={modalOpen}
        onCancel={handleModalClose}
        onOk={handleSubmit}
        confirmLoading={saveMutation.isPending}
        width={800}
      >
        <Tabs defaultActiveKey="info" items={[
          {
            key: 'info',
            label: ' ',
            children: (
              <Form form={form} layout="vertical">
                <Form.Item name="name" label=" " rules={[{ required: true, message: ' ' }]}>
                  <Input placeholder=":  " />
                </Form.Item>
                <Form.Item name="full_name" label=" ">
                  <Input placeholder="  " />
                </Form.Item>
                <Space style={{ display: 'flex' }} align="start">
                  <Form.Item name="inn" label="" style={{ width: 200 }}>
                    <Input />
                  </Form.Item>
                  <Form.Item name="kpp" label="" style={{ width: 200 }}>
                    <Input />
                  </Form.Item>
                </Space>
                <Form.Item name="legal_address" label=" ">
                  <Input.TextArea rows={2} />
                </Form.Item>
                <Form.Item name="actual_address" label=" ">
                  <Input.TextArea rows={2} />
                </Form.Item>
                <Form.Item name="rating" label="">
                  <Rate allowHalf />
                </Form.Item>
                <Form.Item name="notes" label="">
                  <Input.TextArea rows={2} />
                </Form.Item>
              </Form>
            ),
          },
          {
            key: 'contacts',
            label: `  (${contacts.length})`,
            children: (
              <div>
                <Button 
                  type="dashed" 
                  icon={<PlusOutlined />} 
                  onClick={handleAddContact}
                  style={{ marginBottom: 16 }}
                  block
                >
                    
                </Button>
                
                {contacts.length === 0 ? (
                  <Empty description="   " />
                ) : (
                  <List
                    dataSource={contacts}
                    renderItem={(contact, index) => (
                      <List.Item
                        actions={[
                          !contact.is_primary && (
                            <Button 
                              type="link" 
                              size="small" 
                              onClick={() => handleSetPrimaryContact(index)}
                            >
                               
                            </Button>
                          ),
                          <Button 
                            type="link" 
                            size="small" 
                            onClick={() => handleEditContact(contact, index)}
                          >
                            
                          </Button>,
                          <Popconfirm
                            title=" ?"
                            onConfirm={() => handleDeleteContact(index)}
                          >
                            <Button type="link" size="small" danger></Button>
                          </Popconfirm>,
                        ].filter(Boolean)}
                      >
                        <List.Item.Meta
                          avatar={<Avatar icon={<UserOutlined />} />}
                          title={
                            <Space>
                              {getContactFullName(contact)}
                              {contact.is_primary && (
                                <Tag color="gold" icon={<StarFilled />}></Tag>
                              )}
                            </Space>
                          }
                          description={
                            <Space split={<Divider type="vertical" />}>
                              {contact.position && <span>{contact.position}</span>}
                              {contact.phone && <span><PhoneOutlined /> {contact.phone}</span>}
                              {contact.email && <span><MailOutlined /> {contact.email}</span>}
                            </Space>
                          }
                        />
                      </List.Item>
                    )}
                  />
                )}
              </div>
            ),
          },
          {
            key: 'bank',
            label: (
              <Space>
                <BankOutlined />
                  ({bankDetails.length})
              </Space>
            ),
            children: editingSupplier ? (
              <BankDetailsList
                bankDetails={bankDetails}
                supplierId={editingSupplier.id}
              />
            ) : (
              <Empty 
                description=" ,    " 
                style={{ margin: '40px 0' }}
              />
            ),
          },
        ]} />
      </Modal>

      {/*     */}
      <Modal
        title={editingContact ? ' ' : '  '}
        open={contactModalOpen}
        onCancel={() => { setContactModalOpen(false); contactForm.resetFields(); }}
        onOk={handleContactSubmit}
        width={500}
      >
        <Form form={contactForm} layout="vertical">
          <Form.Item 
            name="last_name" 
            label="" 
            rules={[{ required: true, message: ' ' }]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item 
            name="first_name" 
            label="" 
            rules={[{ required: true, message: ' ' }]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item name="middle_name" label="">
            <Input placeholder="" />
          </Form.Item>
          <Form.Item name="position" label="">
            <Input placeholder=":   " />
          </Form.Item>
          <Form.Item name="phone" label="">
            <Input placeholder="+7 (999) 123-45-67" />
          </Form.Item>
          <Form.Item name="email" label="Email">
            <Input placeholder="email@example.com" />
          </Form.Item>
          <Form.Item name="notes" label="">
            <Input.TextArea rows={2} />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
}
