import {
    DeleteOutlined,
    EditOutlined,
    ExclamationCircleOutlined,
    InboxOutlined,
    PlusOutlined,
    SwapOutlined,
} from '@ant-design/icons';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import {
    Alert,
    Button,
    Card,
    Empty,
    Form,
    Input,
    message,
    Modal,
    Select,
    Space,
    Switch,
    Table,
    Tag,
    Typography,
} from 'antd';
import type { ColumnsType } from 'antd/es/table';
import { useMemo, useState } from 'react';

import {
    warehouseApi,
    type Warehouse,
    type WarehouseSummary,
} from '../../features/warehouse';

const { Title, Text } = Typography;

const getApiErrorMessage = (error: any): string | null => {
  const data = error?.response?.data;
  if (!data) return null;
  if (typeof data === 'string') return data;

  const detail = data?.detail || data?.error;
  if (typeof detail === 'string') return detail;

  if (typeof data === 'object') {
    // DRF validation errors often look like: { field: ["msg"], non_field_errors: ["msg"] }
    const parts = Object.entries(data as Record<string, unknown>)
      .map(([key, value]) => {
        if (Array.isArray(value)) return `${key}: ${value.map(String).join(', ')}`;
        if (value === null || value === undefined) return null;
        if (typeof value === 'string') return `${key}: ${value}`;
        return null;
      })
      .filter(Boolean) as string[];

    if (parts.length > 0) return parts.join('; ');
  }

  return null;
};

export default function WarehousesSettingsPage() {
  const queryClient = useQueryClient();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingWarehouse, setEditingWarehouse] = useState<Warehouse | null>(null);
  const [isTransferModalOpen, setIsTransferModalOpen] = useState(false);
  const [deletingWarehouse, setDeletingWarehouse] = useState<Warehouse | null>(null);
  const [warehouseSummary, setWarehouseSummary] = useState<WarehouseSummary | null>(null);

  const [form] = Form.useForm();
  const [transferForm] = Form.useForm();

  // Fetch warehouses
  const { data: warehousesData, isLoading } = useQuery({
    queryKey: ['warehouses-all'],
    queryFn: () => warehouseApi.warehouses.list({ page_size: 100 }),
  });

  const warehouses = warehousesData?.results || [];

  // Create mutation
  const createMutation = useMutation({
    mutationFn: (data: Partial<Warehouse>) => warehouseApi.warehouses.create(data),
    onSuccess: () => {
      message.success(' ');
      setIsModalOpen(false);
      form.resetFields();
      queryClient.invalidateQueries({ queryKey: ['warehouses-all'] });
      queryClient.invalidateQueries({ queryKey: ['warehouses'] });
    },
    onError: (error: any) => {
      const detail = getApiErrorMessage(error);
      message.error(detail ? `   : ${detail}` : '   ');
    },
  });

  // Update mutation
  const updateMutation = useMutation({
    mutationFn: ({ id, data }: { id: string; data: Partial<Warehouse> }) =>
      warehouseApi.warehouses.update(id, data),
    onSuccess: () => {
      message.success(' ');
      setIsModalOpen(false);
      setEditingWarehouse(null);
      form.resetFields();
      queryClient.invalidateQueries({ queryKey: ['warehouses-all'] });
      queryClient.invalidateQueries({ queryKey: ['warehouses'] });
    },
    onError: (error: any) => {
      const detail = getApiErrorMessage(error);
      message.error(detail ? `   : ${detail}` : '   ');
    },
  });

  // Delete mutation
  const deleteMutation = useMutation({
    mutationFn: (id: string) => warehouseApi.warehouses.delete(id),
    onSuccess: () => {
      message.success(' ');
      queryClient.invalidateQueries({ queryKey: ['warehouses-all'] });
      queryClient.invalidateQueries({ queryKey: ['warehouses'] });
    },
    onError: (error: any) => {
      const detail = getApiErrorMessage(error);
      message.error(detail ? `   : ${detail}` : '   ');
    },
  });

  // Create transfer for deletion mutation
  const createTransferMutation = useMutation({
    mutationFn: ({ sourceId, destinationId }: { sourceId: string; destinationId: string }) =>
      warehouseApi.stockTransfers.createForWarehouseDeletion(sourceId, destinationId),
    onSuccess: (result) => {
      message.success(`  ${result.number}.       .`);
      setIsTransferModalOpen(false);
      setDeletingWarehouse(null);
      transferForm.resetFields();
    },
    onError: () => {
      message.error('   ');
    },
  });

  // Handle open modal
  const handleOpenModal = (warehouse?: Warehouse) => {
    if (warehouse) {
      setEditingWarehouse(warehouse);
      form.setFieldsValue({
        name: warehouse.name,
        address: warehouse.address,
        description: warehouse.description,
        is_active: warehouse.is_active,
      });
    } else {
      form.setFieldsValue({
        is_active: true,
      });
    }
    setIsModalOpen(true);
  };

  // Handle close modal
  const handleCloseModal = () => {
    setIsModalOpen(false);
    setEditingWarehouse(null);
    form.resetFields();
  };

  // Handle save
  const handleSave = () => {
    form.validateFields().then((values) => {
      if (editingWarehouse) {
        updateMutation.mutate({ id: editingWarehouse.id, data: values });
      } else {
        createMutation.mutate(values);
      }
    });
  };

  // Handle delete attempt
  const handleDeleteAttempt = async (warehouse: Warehouse) => {
    try {
      const summary = await warehouseApi.warehouses.summary(warehouse.id);
      setWarehouseSummary(summary);
      
      if (summary.total_items > 0) {
        // Has items - show transfer modal
        setDeletingWarehouse(warehouse);
        setIsTransferModalOpen(true);
      } else {
        // No items - can delete directly
        Modal.confirm({
          title: ' ?',
          content: ` ,     "${warehouse.name}"?`,
          okText: '',
          okType: 'danger',
          cancelText: '',
          onOk: () => deleteMutation.mutate(warehouse.id),
        });
      }
    } catch {
      message.error('   ');
    }
  };

  // Handle transfer creation
  const handleCreateTransfer = () => {
    transferForm.validateFields().then((values) => {
      if (deletingWarehouse) {
        createTransferMutation.mutate({
          sourceId: deletingWarehouse.id,
          destinationId: values.destination_warehouse,
        });
      }
    });
  };

  // Table columns
  const columns: ColumnsType<Warehouse> = useMemo(
    () => [
      {
        title: '',
        dataIndex: 'code',
        key: 'code',
        width: 100,
      },
      {
        title: '',
        dataIndex: 'name',
        key: 'name',
        width: 200,
      },
      {
        title: '',
        dataIndex: 'address',
        key: 'address',
        width: 200,
        render: (address: string) => address || '',
      },
      {
        title: '',
        dataIndex: 'description',
        key: 'description',
        width: 200,
        render: (description: string) => description || '',
      },
      {
        title: '',
        dataIndex: 'is_active',
        key: 'status',
        width: 100,
        align: 'center',
        render: (isActive: boolean) => (
          <Tag color={isActive ? 'success' : 'default'}>
            {isActive ? '' : ''}
          </Tag>
        ),
      },
      {
        title: '',
        key: 'actions',
        width: 120,
        align: 'center',
        render: (_, record) => (
          <Space>
            <Button
              type="link"
              size="small"
              icon={<EditOutlined />}
              onClick={() => handleOpenModal(record)}
            />
            <Button
              type="link"
              danger
              size="small"
              icon={<DeleteOutlined />}
              onClick={() => handleDeleteAttempt(record)}
            />
          </Space>
        ),
      },
    ],
    []
  );

  return (
    <div className="page-container">
      <div style={{ marginBottom: 24, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <div>
          <Title level={4} style={{ margin: 0 }}>
            
          </Title>
          <Text type="secondary">  </Text>
        </div>
        <Button type="primary" icon={<PlusOutlined />} onClick={() => handleOpenModal()}>
           
        </Button>
      </div>

      <Card size="small">
        <Table
          columns={columns}
          dataSource={warehouses}
          rowKey="id"
          loading={isLoading}
          pagination={false}
          size="small"
          locale={{
            emptyText: <Empty description=" " />,
          }}
        />
      </Card>

      {/* Create/Edit Modal */}
      <Modal
        title={editingWarehouse ? ' ' : ' '}
        open={isModalOpen}
        onCancel={handleCloseModal}
        onOk={handleSave}
        okText=""
        cancelText=""
        confirmLoading={createMutation.isPending || updateMutation.isPending}
        width={600}
      >
        <Form form={form} layout="vertical">
          <Form.Item
            name="name"
            label=""
            rules={[{ required: true, message: ' ' }]}
          >
            <Input placeholder=" " />
          </Form.Item>

          <Form.Item name="address" label="">
            <Input.TextArea rows={2} placeholder=" " />
          </Form.Item>

          <Form.Item name="description" label="">
            <Input.TextArea rows={2} placeholder="   " />
          </Form.Item>

          <Form.Item name="is_active" label="" valuePropName="checked">
            <Switch />
          </Form.Item>
        </Form>
      </Modal>

      {/* Transfer Modal for deletion */}
      <Modal
        title={
          <Space>
            <ExclamationCircleOutlined style={{ color: '#faad14' }} />
            <span>   </span>
          </Space>
        }
        open={isTransferModalOpen}
        onCancel={() => {
          setIsTransferModalOpen(false);
          setDeletingWarehouse(null);
          setWarehouseSummary(null);
          transferForm.resetFields();
        }}
        onOk={handleCreateTransfer}
        okText=" "
        cancelText=""
        confirmLoading={createTransferMutation.isPending}
        width={500}
      >
        <Alert
          message="  "
          description={
            <>
                <strong>"{deletingWarehouse?.name}"</strong> {' '}
              <strong>{warehouseSummary?.total_items || 0}</strong> .
              <br />
                        .
            </>
          }
          type="warning"
          showIcon
          icon={<InboxOutlined />}
          style={{ marginBottom: 16 }}
        />

        <Form form={transferForm} layout="vertical">
          <Form.Item
            name="destination_warehouse"
            label=" "
            rules={[{ required: true, message: ' ' }]}
          >
            <Select
              placeholder="    "
              options={warehouses
                .filter((w: Warehouse) => w.id !== deletingWarehouse?.id && w.is_active)
                .map((w: Warehouse) => ({
                  label: `${w.name} (${w.code})`,
                  value: w.id,
                }))}
            />
          </Form.Item>
        </Form>

        <Text type="secondary">
          <SwapOutlined />      (   ), 
               .
        </Text>
      </Modal>
    </div>
  );
}
