import { CheckCircleOutlined, DeleteOutlined, EditOutlined, LockOutlined, PlusOutlined, SearchOutlined } from '@ant-design/icons';
import { Button, Card, ColorPicker, Form, Input, InputNumber, message, Modal, Popconfirm, Space, Switch, Table, Tag, Tooltip, Typography } from 'antd';
import type { Color } from 'antd/es/color-picker';
import type { ColumnsType } from 'antd/es/table';
import { useState } from 'react';
import type { ManufacturingStatusRef } from '../../features/settings/api';
import {
    useCreateManufacturingStatus,
    useDeleteManufacturingStatus,
    useManufacturingStatuses,
    useSetDefaultManufacturingStatus,
    useUpdateManufacturingStatus,
} from '../../features/settings/hooks';

const { Title, Text } = Typography;

export default function ManufacturingStatusesPage() {
  const [searchText, setSearchText] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingStatus, setEditingStatus] = useState<ManufacturingStatusRef | null>(null);
  const [form] = Form.useForm();

  const { data: statusesData, isLoading } = useManufacturingStatuses();
  const createMutation = useCreateManufacturingStatus();
  const updateMutation = useUpdateManufacturingStatus();
  const deleteMutation = useDeleteManufacturingStatus();
  const setDefaultMutation = useSetDefaultManufacturingStatus();

  const statuses = statusesData?.results || [];

  const filteredStatuses = statuses.filter(
    (status) =>
      status.name.toLowerCase().includes(searchText.toLowerCase()) ||
      status.code.toLowerCase().includes(searchText.toLowerCase())
  );

  const handleOpenModal = (status?: ManufacturingStatusRef) => {
    if (status) {
      setEditingStatus(status);
      form.setFieldsValue({
        ...status,
        color: status.color,
      });
    } else {
      setEditingStatus(null);
      form.resetFields();
      form.setFieldsValue({
        color: '#1890ff',
        sort_order: (statuses.length + 1) * 10,
        progress_percent: 0,
        is_default: false,
        is_completed: false,
      });
    }
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setEditingStatus(null);
    form.resetFields();
  };

  const handleSubmit = async (values: any) => {
    try {
      const colorValue = typeof values.color === 'string' 
        ? values.color 
        : (values.color as Color)?.toHexString?.() || '#1890ff';

      const data = {
        ...values,
        color: colorValue,
      };

      if (editingStatus) {
        await updateMutation.mutateAsync({ id: editingStatus.id, data });
        message.success('  ');
      } else {
        await createMutation.mutateAsync(data);
        message.success('  ');
      }
      handleCloseModal();
    } catch (error: any) {
      message.error(error.message || ' ');
    }
  };

  const handleDelete = async (id: string) => {
    try {
      await deleteMutation.mutateAsync(id);
      message.success(' ');
    } catch (error: any) {
      message.error(error.message || ' ');
    }
  };

  const handleSetDefault = async (id: string) => {
    try {
      await setDefaultMutation.mutateAsync(id);
      message.success('   ');
    } catch (error: any) {
      message.error(error.message || '');
    }
  };

  const columns: ColumnsType<ManufacturingStatusRef> = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      render: (text, record) => (
        <Space>
          <Tag color={record.color}>{text}</Tag>
          {record.is_system && (
            <Tooltip title="  ( )">
              <LockOutlined style={{ color: '#999' }} />
            </Tooltip>
          )}
        </Space>
      ),
    },
    {
      title: '',
      dataIndex: 'progress_percent',
      key: 'progress_percent',
      width: 100,
      align: 'center',
      render: (value) => `${value}%`,
    },
    {
      title: '',
      dataIndex: 'is_completed',
      key: 'is_completed',
      width: 110,
      align: 'center',
      render: (value) => value ? <Tag color="green"></Tag> : <Tag></Tag>,
    },
    {
      title: ' ',
      dataIndex: 'is_default',
      key: 'is_default',
      width: 120,
      align: 'center',
      render: (value, record) => (
        value ? (
          <Tag color="blue" icon={<CheckCircleOutlined />}> </Tag>
        ) : (
          <Button 
            type="link" 
            size="small" 
            onClick={() => handleSetDefault(record.id)}
          >
              
          </Button>
        )
      ),
    },
    {
      title: '',
      dataIndex: 'sort_order',
      key: 'sort_order',
      width: 80,
      align: 'center',
      sorter: (a, b) => a.sort_order - b.sort_order,
    },
    {
      title: '',
      key: 'actions',
      width: 100,
      align: 'center',
      render: (_, record) => (
        <Space size="small">
          <Tooltip title="">
            <Button
              type="text"
              icon={<EditOutlined />}
              onClick={() => handleOpenModal(record)}
            />
          </Tooltip>
          {!record.is_system && (
            <Popconfirm
              title=" ?"
              description="   "
              onConfirm={() => handleDelete(record.id)}
              okText=""
              cancelText=""
            >
              <Tooltip title="">
                <Button type="text" danger icon={<DeleteOutlined />} />
              </Tooltip>
            </Popconfirm>
          )}
        </Space>
      ),
    },
  ];

  return (
    <div className="page-container">
      <div style={{ marginBottom: 24, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <div>
          <Title level={4} style={{ margin: 0 }}> </Title>
          <Text type="secondary">    </Text>
        </div>
        <Button type="primary" icon={<PlusOutlined />} onClick={() => handleOpenModal()}>
           
        </Button>
      </div>

      <Card size="small" style={{ marginBottom: 16 }}>
        <Space wrap>
          <Input
            placeholder="    ..."
            prefix={<SearchOutlined />}
            style={{ width: 300 }}
            allowClear
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
          />
        </Space>
      </Card>

      <Card size="small">
        <Table
          columns={columns}
          dataSource={filteredStatuses}
          rowKey="id"
          loading={isLoading}
          pagination={false}
          size="small"
        />
      </Card>

      <Modal
        title={editingStatus ? ' ' : '  '}
        open={isModalOpen}
        onCancel={handleCloseModal}
        footer={null}
        width={600}
      >
        <Form
          form={form}
          layout="vertical"
          onFinish={handleSubmit}
          style={{ marginTop: 16 }}
        >
          <Form.Item
            name="name"
            label=""
            rules={[
              { required: true, message: ' ' },
              { max: 100, message: ' 100 ' },
            ]}
          >
            <Input placeholder=" " />
          </Form.Item>

          <Form.Item name="description" label="">
            <Input.TextArea rows={2} placeholder=" ..." />
          </Form.Item>

          <Space size="large" style={{ display: 'flex' }}>
            <Form.Item name="color" label="">
              <ColorPicker />
            </Form.Item>

            <Form.Item
              name="sort_order"
              label=" "
              rules={[{ required: true, message: ' ' }]}
            >
              <InputNumber min={0} max={1000} />
            </Form.Item>

            <Form.Item
              name="progress_percent"
              label=" (%)"
              rules={[{ required: true, message: ' ' }]}
            >
              <InputNumber min={0} max={100} />
            </Form.Item>
          </Space>

          <Space size="large">
            <Form.Item name="is_default" valuePropName="checked" label=" ">
              <Switch />
            </Form.Item>

            <Form.Item name="is_completed" valuePropName="checked" label=" ">
              <Switch />
            </Form.Item>
          </Space>

          <Form.Item name="auto_trigger" label="-">
            <Input placeholder="   " />
          </Form.Item>

          <Form.Item style={{ marginBottom: 0, marginTop: 24 }}>
            <Space>
              <Button onClick={handleCloseModal}></Button>
              <Button
                type="primary"
                htmlType="submit"
                loading={createMutation.isPending || updateMutation.isPending}
              >
                {editingStatus ? '' : ''}
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
}
