import {
    BarChartOutlined,
    DashboardOutlined,
    DatabaseOutlined,
    InboxOutlined,
    LaptopOutlined,
    ProjectOutlined,
    SettingOutlined,
    ShoppingCartOutlined,
} from '@ant-design/icons';
import { Layout, Menu, type MenuProps } from 'antd';
import { useLocation, useNavigate } from 'react-router-dom';

import { useAuth } from '../../../app/providers/AuthProvider';

const { Sider } = Layout;

interface AppSidebarProps {
  collapsed: boolean;
  onCollapse: (collapsed: boolean) => void;
}

type MenuItem = Required<MenuProps>['items'][number];

function getItem(
  label: React.ReactNode,
  key: string,
  icon?: React.ReactNode,
  children?: MenuItem[],
  roles?: string[]
): MenuItem {
  return {
    key,
    icon,
    children,
    label,
    roles,
  } as MenuItem;
}

export function AppSidebar({ collapsed, onCollapse }: AppSidebarProps) {
  const location = useLocation();
  const navigate = useNavigate();
  const { user } = useAuth();

  // Get user's role codes from user_roles
  const userRoles = user?.user_roles?.map((r) => r.role_detail?.code).filter(Boolean) || [];
  const isAdmin = userRoles.includes('admin') || user?.is_superuser;

  // Filter menu items based on roles
  const filterByRoles = (items: MenuItem[]): MenuItem[] => {
    return items.filter((item) => {
      if (!item) return false;
      const itemRoles = (item as any).roles as string[] | undefined;
      if (!itemRoles) return true;
      return isAdmin || itemRoles.some((role) => userRoles.includes(role));
    });
  };

  const menuItems: MenuItem[] = filterByRoles([
    getItem(' ', '/', <DashboardOutlined />),
    getItem('', '/projects', <ProjectOutlined />, [
      getItem('', '/projects?status=in_progress'),
      getItem(' ', '/projects'),
      getItem('', '/projects?status=completed'),
    ], ['admin', 'project_manager', 'engineer', 'production']),
    getItem('', '/catalog', <DatabaseOutlined />, [
      getItem('', '/catalog/nomenclature'),
      getItem('', '/catalog/suppliers'),
      getItem('', '/catalog/contractors'),
      { type: 'divider' },
      getItem(' ', '/catalog/settings'),
    ], ['admin', 'engineer', 'procurement']),
    //  -  ERP-
    getItem('', '/procurement', <ShoppingCartOutlined />, [
      getItem('', '/procurement/requirements'),
      getItem('  ', '/procurement/orders'),
    ], ['admin', 'procurement']),
    getItem(' ', '/workplace', <LaptopOutlined />, undefined, ['admin', 'production', 'engineer', 'project_manager']),
    //  -  ERP-
    getItem('', '/warehouse', <InboxOutlined />, [
      getItem('', '/warehouse'),
      getItem('', '/warehouse/receipts'),
      getItem(' ', '/warehouse/movements'),
      getItem('', '/warehouse/transfers'),
      getItem(' ', '/warehouse/contractor-writeoffs'),
      getItem('  ', '/warehouse/contractor-receipts'),
      getItem('', '/warehouse/inventory'),
    ], ['admin', 'warehouse', 'procurement']),
    getItem('', '/analytics', <BarChartOutlined />, undefined, ['admin', 'project_manager']),
    { type: 'divider' },
    getItem('', '/settings', <SettingOutlined />, [
      getItem('', '/settings/users'),
      getItem('', '/settings/roles'),
      getItem('', '/settings/warehouses'),
      { type: 'divider' },
      getItem('', '/settings/system'),
      getItem(' ', '/settings/manufacturing-statuses'),
      getItem(' ', '/settings/purchase-statuses'),
      getItem(' ', '/settings/manufacturing-problem-reasons'),
      getItem(' ', '/settings/purchase-problem-reasons'),
    ], ['admin']),
  ]);

  const handleMenuClick: MenuProps['onClick'] = (e) => {
    navigate(e.key);
  };

  // Get selected keys based on current path
  const getSelectedKeys = () => {
    const path = location.pathname;

    // Projects list supports status filtering via querystring
    if (path === '/projects') {
      const params = new URLSearchParams(location.search);
      const status = params.get('status');
      if (status === 'in_progress') return ['/projects?status=in_progress'];
      if (status === 'completed') return ['/projects?status=completed'];
      return ['/projects'];
    }

    // Handle nested project routes (detail/tabs)
    if (path.startsWith('/projects/')) return ['/projects'];
    if (path.startsWith('/catalog/')) return [path];
    if (path.startsWith('/settings/')) return [path];
    return [path];
  };

  // Get open keys for submenus
  const getOpenKeys = () => {
    const path = location.pathname;
    if (path.startsWith('/projects')) return ['/projects'];
    if (path.startsWith('/catalog')) return ['/catalog'];
    if (path.startsWith('/procurement')) return ['/procurement'];
    if (path.startsWith('/workplace')) return ['/workplace'];
    if (path.startsWith('/warehouse')) return ['/warehouse'];
    if (path.startsWith('/settings')) return ['/settings'];
    return [];
  };

  return (
    <Sider
      collapsible
      collapsed={collapsed}
      onCollapse={onCollapse}
      width={240}
      collapsedWidth={64}
      style={{
        background: '#fff',
        borderRight: '1px solid #f0f0f0',
        position: 'fixed',
        left: 0,
        top: 0,
        bottom: 0,
        zIndex: 100,
        overflow: 'auto',
      }}
      trigger={null}
    >
      {/* Logo */}
      <div
        style={{
          height: 56,
          display: 'flex',
          alignItems: 'center',
          justifyContent: collapsed ? 'center' : 'flex-start',
          padding: collapsed ? 0 : '0 16px',
          borderBottom: '1px solid #f0f0f0',
          fontWeight: 600,
          fontSize: 18,
          color: '#1890ff',
          cursor: 'pointer',
        }}
        onClick={() => navigate('/')}
      >
        {collapsed ? '' : ' PDM ERP'}
      </div>

      {/* Navigation Menu */}
      <Menu
        mode="inline"
        selectedKeys={getSelectedKeys()}
        defaultOpenKeys={collapsed ? [] : getOpenKeys()}
        items={menuItems}
        onClick={handleMenuClick}
        style={{
          border: 'none',
          marginTop: 8,
        }}
      />
    </Sider>
  );
}
