import { useMutation, useQueryClient } from '@tanstack/react-query';
import { Form, Input, Modal, Select, Switch, message } from 'antd';
import React from 'react';
import { catalogApi, type BankDetails, type Currency } from '../api';

interface BankDetailsModalProps {
  open: boolean;
  onClose: () => void;
  bankDetail?: BankDetails | null;
  supplierId?: string;
  contractorId?: string;
}

const CURRENCY_OPTIONS: { value: Currency; label: string }[] = [
  { value: 'RUB', label: ' ' },
  { value: 'USD', label: '$  ' },
  { value: 'EUR', label: ' ' },
  { value: 'CNY', label: ' ' },
];

export const BankDetailsModal: React.FC<BankDetailsModalProps> = ({
  open,
  onClose,
  bankDetail,
  supplierId,
  contractorId,
}) => {
  const [form] = Form.useForm();
  const queryClient = useQueryClient();
  const isEdit = !!bankDetail;

  const createMutation = useMutation({
    mutationFn: (data: Partial<BankDetails>) => catalogApi.bankDetails.create(data),
    onSuccess: () => {
      message.success('  ');
      queryClient.invalidateQueries({ queryKey: ['bank-details'] });
      if (supplierId) {
        queryClient.invalidateQueries({ queryKey: ['suppliers', supplierId] });
      }
      if (contractorId) {
        queryClient.invalidateQueries({ queryKey: ['contractors', contractorId] });
      }
      onClose();
      form.resetFields();
    },
    onError: () => {
      message.error('   ');
    },
  });

  const updateMutation = useMutation({
    mutationFn: (data: Partial<BankDetails>) =>
      catalogApi.bankDetails.update(bankDetail!.id, data),
    onSuccess: () => {
      message.success('  ');
      queryClient.invalidateQueries({ queryKey: ['bank-details'] });
      if (supplierId) {
        queryClient.invalidateQueries({ queryKey: ['suppliers', supplierId] });
      }
      if (contractorId) {
        queryClient.invalidateQueries({ queryKey: ['contractors', contractorId] });
      }
      onClose();
      form.resetFields();
    },
    onError: () => {
      message.error('   ');
    },
  });

  const handleSubmit = async () => {
    try {
      const values = await form.validateFields();
      
      const data: Partial<BankDetails> = {
        ...values,
        supplier: supplierId,
        contractor: contractorId,
      };

      if (isEdit) {
        updateMutation.mutate(data);
      } else {
        createMutation.mutate(data);
      }
    } catch (error) {
      console.error('Validation failed:', error);
    }
  };

  React.useEffect(() => {
    if (open && bankDetail) {
      form.setFieldsValue({
        bank_name: bankDetail.bank_name,
        bik: bankDetail.bik,
        correspondent_account: bankDetail.correspondent_account,
        settlement_account: bankDetail.settlement_account,
        currency: bankDetail.currency,
        is_primary: bankDetail.is_primary,
        notes: bankDetail.notes,
      });
    } else if (open) {
      form.resetFields();
      form.setFieldsValue({
        currency: 'RUB',
        is_primary: false,
      });
    }
  }, [open, bankDetail, form]);

  const isLoading = createMutation.isPending || updateMutation.isPending;

  return (
    <Modal
      title={isEdit ? ' ' : '  '}
      open={open}
      onOk={handleSubmit}
      onCancel={onClose}
      okText={isEdit ? '' : ''}
      cancelText=""
      confirmLoading={isLoading}
      width={600}
    >
      <Form
        form={form}
        layout="vertical"
        initialValues={{
          currency: 'RUB',
          is_primary: false,
        }}
      >
        <Form.Item
          name="bank_name"
          label=" "
          rules={[{ required: true, message: '  ' }]}
        >
          <Input placeholder=" " />
        </Form.Item>

        <Form.Item
          name="bik"
          label=""
          rules={[
            { required: true, message: ' ' },
            { len: 9, message: '   9 ' },
            { pattern: /^\d+$/, message: '    ' },
          ]}
        >
          <Input placeholder="044525225" maxLength={9} />
        </Form.Item>

        <Form.Item
          name="correspondent_account"
          label=" "
          rules={[
            { required: true, message: ' . ' },
            { len: 20, message: '.    20 ' },
            { pattern: /^\d+$/, message: '.     ' },
          ]}
        >
          <Input placeholder="30101810400000000225" maxLength={20} />
        </Form.Item>

        <Form.Item
          name="settlement_account"
          label=" "
          rules={[
            { required: true, message: '  ' },
            { len: 20, message: '    20 ' },
            { pattern: /^\d+$/, message: '     ' },
          ]}
        >
          <Input placeholder="40702810438000000001" maxLength={20} />
        </Form.Item>

        <Form.Item
          name="currency"
          label=""
          rules={[{ required: true, message: ' ' }]}
        >
          <Select options={CURRENCY_OPTIONS} />
        </Form.Item>

        <Form.Item
          name="is_primary"
          label=" "
          valuePropName="checked"
        >
          <Switch />
        </Form.Item>

        <Form.Item
          name="notes"
          label=""
        >
          <Input.TextArea rows={3} placeholder=" " />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default BankDetailsModal;
